    font-size: 18px;
  }

  .desc {
    font-size: 18px;
  }

  .direction-l {
    position: relative;
    width: 550px;
    float: left;
    text-align: right;
  }

  .direction-r {
    position: relative;
    width: 550px;
    float: right;
    text-align: left;
  }
}




